.wrapper {
    display: grid;
    grid-template-columns: 8fr 11fr 9fr;
    -webkit-align-items: center;
            align-items: center;
    justify-items: center;
}

.title {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 40px;
    color:#FFDBFF;
    font-family: cursive;
    padding:10px 20px;
}

.title-cookie {
    margin-left: 4px;
}

.search-input {
    min-width: 240px;
    padding:10px 4px;
    outline:none;
    border:none;
    background-color: whitesmoke;
    border-top-left-radius: 6px;
    border-bottom-left-radius: 6px;
}

.search-form {
    box-shadow: 0px 0px 4px 1px black;
