}

.search-input:focus::placeholder {
    -webkit-transform:translateY(-40px);
            transform:translateY(-40px)
}

.search-button {
    padding:10px 8px;
    outline:none;
    border:none;
    cursor:pointer;
    border-top-right-radius: 6px;
    border-bottom-right-radius: 6px;
