@media (max-width:1280px) {
    .wrapper {
        grid-template-columns: 1fr 1fr;
    }

    .open-mobile-menu {
        display: block;
    }

    .menu-list {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
        position: fixed;
        left:-100%;
    }

    .search input {
        min-width:0px;
    }

    .menu-list__item:not(:last-child) {
        margin:0px 0px 12px 0px;
    }

    .menu-list-active {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
        left:0;
        position: fixed;
        bottom: 0;
        width: 100%;
        height: 100%;
        background-color: black;
        z-index: 2;
        transition: all 0.5s ease;
        box-shadow: 0px 0px 4px 1px solid black;
    }

    .menu-list-active .menu-list__item {
        width:84vw;
        border:1px solid white;
        transition: 0.5s all ease;
    }

    .menu-list-active .menu-list__item:hover {
        background-color: #fff;
    }

    .close-mobile-menu, .open-mobile-menu {
        cursor: pointer;
        font-size:32px;
        margin-left: 10px;
    }

    .close-mobile-menu {
        color:white;
    }

    .close-mobile-menu-wrapper {
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: center;
                justify-content: center;
        cursor:pointer;
        transition: 0.5s all ease;
    }

    .close-mobile-menu-wrapper:hover {
        box-shadow:0px 0px 10px 4px #999;
    }

    .close-mobile-menu-wrapper:hover .close-mobile-menu {
        color:black;
    }

}

@media (max-width:768px){
    .wrapper {
        grid-template-columns: none;
        grid-template-rows:1fr 1fr;
        position: relative;
    }

    .title {
        font-size:34px;
    }
    
    .menu-list {
        position: absolute;
        left: -250px;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        margin-bottom: 10px;
    }

    .menu-list__item:not(:last-child) {
        margin-right: 0px;
        margin-bottom: 8px;
    }

    .search input {
        min-width:0px;
    }
}

